import { videoapi } from './api-service';
const { error } = console;

const save = (key, obj) => {
  try {
    localStorage.setItem(key, JSON.stringify(obj));
  } catch (err) {
    error(err);
  }
};

const load = key => {
  try {
    const isStored = localStorage.getItem(key);
    return isStored === null ? undefined : JSON.parse(isStored);
  } catch (err) {
    error(err);
  }
};

const removeEmptyStorageKeys = async () => {
  try {
    Object.keys(localStorage).forEach(key => {
      const loadedData = load(key);

      if (
        !loadedData ||
        loadedData === '' ||
        Object.keys(loadedData).length === 0 ||
        loadedData.length === 0
      ) {
        localStorage.removeItem(key);
        console.log('removeEmptyStorageKeys ~ key', key);
      }
    });
  } catch (err) {
    error(err);
  }
};

export { save, load, removeEmptyStorageKeys };
